<template>
    <div class='text_person'>
        <div class="header">
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="personHead">个人信息</div>
            <div class='save' @click='personSave()'>保存</div>
        </div>
        <div class='personContainer'>
            <div>
                <span class='personLeft'>姓名</span>
                <span style='line-height:0.48rem;color:#999'>{{name}}</span>
            </div>
            <div>
                <span class='personLeft'>电话</span>
                <span style='line-height:0.48rem;'><input type="text" v-model="tel" style='text-align:right;outline:none;color:#333333;' ref="tel" maxlength="11"></span>
            </div>
            <div>
                <span class='personLeft'>邮箱</span>
                <span style='line-height:0.48rem;'><input  type="text" v-model="email" style='text-align:right;outline:none;color:#333333;width:5.5rem;' maxlength="25"></span>
            </div>
            <div>
                <span class='personLeft'>公司</span>
                <span style='line-height:0.48rem;color:#999'>{{companyName}}</span>
            </div>
            <div>
                <span class='personLeft'>部门</span>
                <span style='line-height:0.48rem;color:#999'>{{branchName}}</span>
            </div>
        </div>
        <div class="pwdError" v-show="isshowerror">
            <img src="../../assets/image/密码错误.png" style="position: relative;width: 4.3rem;height: 0.76rem;">
            <div class="pwdError-mes">{{mess}}</div>
        </div>
        <!-- <div class='bigBox'>
            <div class='smallBox'>
                <img src="../../assets/image/弹窗.png" alt="">
                <div class='boxWord'>您编辑的内容还未保存<br/>确定返回?</div>
                <div class="button">
                    <div class="cancel" @click.stop="cancel()">取消</div>
                    <div class="comfirm" @click.stop="comfirm()">确定</div>
                </div>
            </div>
        </div> -->
        <!-- 编辑返回 -->
        <div class='bigBox'>
            <div class='smallBox'>
                <img src="../../assets/image/开始考试的背景.png" alt="">
                <div class='boxWord'>您编辑的内容还未保存<br/>确定返回?</div>
                <div class="text-list-start-startText-mess-two">
                    <div class="text-list-start-startText-mess-two-wait" @click="cancel">取消</div>
                    <div style="width:0.021rem;height:0.42rem;background:rgba(231,236,244,1);margin-top:0.3rem;"></div>
                    <div class="text-list-start-startText-mess-two-go" @click="comfirm">确定</div>
                </div>
            </div>
        </div>
        <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>
<script>
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'
export default {
  components: {
    Loading
  },
  data () {
    return {
      name: '', // 名称
      email: '', // 邮箱
      tel: '', // 电话
      branchName: '', // 部门
      companyName: '', // 公司
      mess: '', // 提示信息
      isshowerror: false,
      initialTel: '', // 最初的电话
      initialEmail: '', // 最初的邮箱
      verDialogVisible: true // 加载页
    }
  },
  created () {
    this.getPerAndOrgByUserId()
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
    // this.$parent.isShow = 3;
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  methods: {
    // 返回
    back () {
      if ($('.save').css('opacity') === '1') {
        $('.bigBox').css('display', 'flex')
      } else {
        this.$router.back()
      }
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    },
    // 取消按钮
    cancel () {
      $('.bigBox').css('display', 'none')
    },
    // 确定按钮
    comfirm () {
      $('.bigBox').css('display', 'none')
      this.$router.back()
    },
    // 获取个人信息
    getPerAndOrgByUserId () {
      API.post('getPerAndOrgByUserId', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.personId = data.data.personnel.id
          this.name = data.data.personnel.name
          this.email = data.data.personnel.email
          this.tel = data.data.personnel.tel
          this.branchName = data.data.branchName
          this.companyName = data.data.companyName
          this.initialTel = data.data.personnel.tel
          this.initialEmail = data.data.personnel.email
          this.verDialogVisible = false
        }
      })
    },
    // 个人信息保存
    getModify () {
      API.post('modify', {
        tel: this.tel,
        email: this.email,
        id: this.personId
      }, 'json', (data, err) => {
        // console.log(data)
        if (data.success) {
          this.$router.back()
        } else {
          alert(data.msg)
        }
      })
    },
    // 保存
    personSave () {
      if ($('.save').css('opacity') === '1') {
        if (this.tel === '' || this.tel === null) {
          this.mess = '电话不能为空，请输入'
          // this.isshowerror = true
          this.$toast(this.mess)
          const that = this
          setTimeout(function () { that.isshowerror = false }, 5000)
          return
        }
        if (this.tel !== '' && this.tel !== null) {
          var regtel = /^[1][3,4,5,7,8][0-9]{9}$/
          // this.$refs.tel.value.length
          if (!regtel.test(this.tel)) {
            this.mess = '电话格式不正确，请重新输入'
            // this.isshowerror = true
            this.$toast(this.mess)
            const that = this
            setTimeout(function () { that.isshowerror = false }, 5000)
            return
          }
        }
        // console.log(this.email)
        if (this.email === '' || this.email === null) {
          this.mess = '邮箱不能为空，请输入'
          // this.isshowerror = true
          this.$toast(this.mess)
          const that = this
          setTimeout(function () { that.isshowerror = false }, 5000)
          return
        }
        if (this.email !== '' && this.email !== null) {
          var regemail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
          if (!regemail.test(this.email)) {
            this.mess = '邮箱格式不正确，请重新输入'
            // this.isshowerror = true
            this.$toast(this.mess)
            const that = this
            setTimeout(function () { that.isshowerror = false }, 5000)
            return
          }
        }
        this.getModify()
      }
    }
    // // 改变电话
    // inputTel(){
    //     this.isshowerror = false;
    // },
    // //改变邮箱
    // inputEmail(){
    //     this.isshowerror = false;
    // }
  },
  watch: {
    tel (val, oldval) {
      // console.log(this.tel)
      if (val !== this.initialTel) {
        this.tel = this.tel.replace(/[^\d]/g, '')
      }

      this.isshowerror = false
      if (this.tel !== this.initialTel || this.email !== this.initialEmail) {
        $('.save').css('opacity', '1')
        $('.save').css('color', '#6E747E')
      } else {
        $('.save').css('opacity', '0.7')
        $('.save').css('color', '#C2CBD6')
      }
    },
    email (val, oldval) {
      if (val !== this.initialEmail) {
        this.email = this.email.replace(/[\u4e00-\u9fa5]/g, '')
        this.email = this.email.replace(/\s+/g, '')
      }
      this.isshowerror = false
      if (this.email !== this.initialEmail || this.tel !== this.initialTel) {
        $('.save').css('opacity', '1')
        $('.save').css('color', '#6E747E')
      } else {
        $('.save').css('opacity', '0.7')
        $('.save').css('color', '#C2CBD6')
      }
    }
  }
}
</script>
<style scoped>
    .text_person{
        width:100%;
        height:100vh;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        background:rgba(245,246,248,1);
    }
    .header{
        width:100%;
        background:#fff;
        font-size:0.38rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        z-index: 99;
        text-align: center;
        border-bottom: 0.012rem solid  rgba(222,230,242,1);
    }
    .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .back img{
        width:0.19rem;
