        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
    }
    .personContainer{
        margin-top:0.94rem;
        background: rgba(255,255,255,1);
        width:100%;
    }
    .personContainer div{
        padding:0 0.35rem;
        height:1.06rem;
        border-bottom:0.012rem solid rgba(238,238,238,1);
        display: flex;
        align-items: center;
        font-size:0.3rem;
        justify-content: space-between;
    }
    .personContainer div:last-child{
        /* height:1.6rem; */
        border:0px
    }
    .personContainer div .personLeft{
        color:#6E747E;
        display:inline-block;
        /* width:1.07rem; */
    }
    .pwdError{
        height: 0.76rem;
        width: 100%;
        margin-top: 1.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .pwdError-mes{
        color: #fff;
        font-size: 0.24rem;
        position: absolute
    }
    /* .bigBox{
        z-index: 99;
        width:100%;
        height:100%;
        background:rgba(0,0,0,0.7);
        position: fixed;
        top:0;
        display: none;
        justify-content: center;
        align-items: center;
    }
    .smallBox{
        position: relative;
        z-index: 999;
        width:5.5rem;
        height:3.48rem;
    }
    .smallBox img{
        width:5.5rem;
        height:3.48rem;
    }
    .smallBox .button{
        width:100%;
        height:0.78rem;
        position:absolute;
        bottom:0.5rem;
        left:0;
        text-align:center;
        z-index:1000;
        padding:0 0.4rem;
        display: flex;
        justify-content: space-between;
    }
    .smallBox .boxWord{
        position:absolute;
        width:100%;
        display: flex;
        justify-content: center;
        text-align: center;
        top:0.25rem;
        left:0;
        z-index: 1000;
        font-size:0.34rem;
        color:#fff;
        text-shadow:0px 3px 4px rgba(12,101,93,0.36);
        letter-spacing:0.04rem;
        line-height: 0.48rem;
    }
    .button div{
        width:2.18rem;
        height:0.78rem;
        font-size:0.3rem;
    }
    .button .cancel{
        color:#666666;
        background:#ffffff;
        border:0.02rem solid rgba(221,221,221,1);
        border-radius: 0.39rem;
        outline: none;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .button .comfirm{
        color:#ffffff;
        background:#04CD9D ;
        border-radius: 0.39rem;
        border:0.02rem solid #04CD9D;
        outline: none;
        display: flex;
        align-items: center;
        justify-content: center;
    } */
    .bigBox{
        z-index: 99;
        width:100%;
        height:100%;
        background:rgba(0,0,0,0.7);
        position: fixed;
        top:0;
        display: none;
        justify-content: center;
        align-items: center;
    }
    .smallBox{
        position: relative;
        z-index: 999;
        width:5.5rem;
        height:2.74rem;
    }
    .smallBox img{
        width:5.5rem;
        height:2.74rem;
    }
    .smallBox .boxWord{
        position:absolute;
        width:100%;
        /* height:1.74rem; */
        display: flex;
        justify-content: center;
        /* align-items: center; */
        text-align: center;
        top:0.42rem;
        left:0;
        z-index: 1000;
        font-size:0.34rem;
        color:#fff;
        letter-spacing:0.04rem;
        color: rgba(78,78,78,1);
        line-height: 0.48rem;
    }
    .smallBox .button{
        width:100%;
        height:0.78rem;
        position:absolute;
        bottom:0.5rem;
        left:0;
        text-align:center;
        z-index:1000;
        padding:0 0.4rem;
        display: flex;
        justify-content: space-between
    }
    .button div{
        width:2.18rem;
        height:0.78rem;
        /* display: inline-block; */
